import React from "react";
import { useNavigate } from "react-router-dom";

function HomePage(){
let navigate = useNavigate();
    return (
      <div className="homeBackgroundImage">
        <div className="mainContainer">
          <div className="mainWrapper mainBeer">
            <h2>Beer</h2>

            <img src="/image/main/  .jpg" alt="  "
             onClick={() => {
                navigate("/beerList");
              }} />
          </div>

          <div className="mainWrapper mainWhiskey">
            <h2>Whiskey</h2>

            <img src="/image/main/  .jpg" 
             onClick={() => {
                navigate("/whiskeyList");
              }}/>
          
          </div>

          <div className="mainWrapper mainCocktail">
            <h2>Cocktail</h2>
            <img
              src="/image/main/  .jpg"
              onClick={() => {
                navigate("/cockTailList");
              }}
            />
          
          </div>
        </div>
      </div>
    );
}

export default HomePage;