import React,{useState,useEffect} from "react";
import Paging from "./pagination";
import { useSelector } from "react-redux";


function Beer({ searchTerm }) {
  const beer = useSelector((state) => state.beer);
  const [currentPage, setCurrentPage] = useState(1);
  const itemsPerPage = 3;
  const indexOfLastItem = currentPage * itemsPerPage;
  const indexOfFirstItem = indexOfLastItem - itemsPerPage;

  const currentItems = searchTerm
    ? beer.filter((beers) =>
        beers.name.toLowerCase().includes(searchTerm.toLowerCase())
      )
    : beer;

const totalItemsCount = currentItems.length;


  //       
  const handlePageChange = (pageNumber) => {
    setCurrentPage(pageNumber);
  };
  useEffect(() => {
    setCurrentPage(1);
  }, [searchTerm]);

  return (
    <>
      <div>
        {currentItems.length === 0 ? (
          <h2 className="searchFailed">   .</h2>
        ) : (
          currentItems.slice(indexOfFirstItem, indexOfLastItem).map((beers) => (
            <div key={beers.id}>
              <div className="pageList" style={{ background: "#c3e2dd" }}>
                <div className="pageImgBox">
                  <img
                    src={`/image/beer/${beers.id}.jpg`}
                    alt={beers.name}
                  ></img>
                </div>
                <div className="pageInfo">
                  <h3 className="pageTitle"> : {beers.name}</h3>
                  <p> : {beers.type}</p>
                  {beers.quality && <p> : {beers.quality}</p>}
                  {beers.taste && <p> : {beers.taste}</p>}
                  <p> :  {beers.vol}</p>
                  <a href={beers.info}></a>
                </div>
              </div>
            </div>
          ))
        )}
        <Paging
          totalItems={totalItemsCount}
          itemsPerPage={itemsPerPage}
          onPageChange={handlePageChange}
        />
      </div>
    </>
  );
}

export default Beer;